import React, {useRef, useEffect} from 'react';
import { Container, Grid, Typography } from '@mui/material';
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

import DrawIcon from '@mui/icons-material/Draw';
import AnimationIcon from '@mui/icons-material/Animation';
import MovieFilterIcon from '@mui/icons-material/MovieFilter';
import CodeIcon from '@mui/icons-material/Code';
import InsightsIcon from '@mui/icons-material/Insights';
import PolylineIcon from '@mui/icons-material/Polyline';

import ItemDiv from "./ItemDiv";

gsap.registerPlugin(ScrollTrigger);

const serviceItems = [
    {
        id: 0,
        icon: <PolylineIcon sx={{fontSize:"70px", mb:2, color:"#ff97cc"}} />,
        heading: "UI/UX",
        body: "Landing Pages, User Flow, Prototyping",
        className: "uiux"
    },
    {
        id: 1,
        icon: <CodeIcon sx={{fontSize:"70px", mb:2, color:"#ff97cc"}} />,
        heading: "Development",
        body: "React, Material UI, CSS, Responsive Sites",
        className: "dev"
    },
    {
        id: 2,
        icon: <DrawIcon sx={{fontSize:"70px", mb:2, color:"#ff97cc"}} />,
        heading: "Illustration",
        body: "Character Design, Icon Sets, Banner Illustrations",
        className: "ill"
    },
    {
        id: 3,
        icon: <InsightsIcon sx={{fontSize:"70px", mb:2, color:"#ff97cc"}} />,
        heading: "Analytics",
        body: "Google Analytics and Google Tag Manager Integration",
        className: "ana"
    },
    {
        id: 4,
        icon: <AnimationIcon sx={{fontSize:"70px", mb:2, color:"#ff97cc"}} />,
        heading: "Animation",
        body: "SVG Animation, UI Motion",
        className: "ani"
    },
    {
        id: 5,
        icon: <MovieFilterIcon sx={{fontSize:"70px", mb:2, color:"#ff97cc"}} />,
        heading: "Videography",
        body: "Product Photography and Short Form Video Content",
        className: "vid"
    },
]

const Services = () => {
    
    const serviceRef = useRef(null);
    const tl = useRef(null);

    useEffect(() => {
        
        let ctx = gsap.context(() => {
            
            gsap.set(".uiux", {opacity:0})
            gsap.set(".dev", {opacity:0})
            gsap.set(".ill", {opacity:0})
            gsap.set(".ana", {opacity:0})
            gsap.set(".ani", {opacity:0})
            gsap.set(".vid", {opacity:0})

            tl.current = gsap.timeline({
                scrollTrigger: {
                    trigger: ".serviceGrid",
                    toggleActions: "restart pause resume none"
                }
            })

            tl.current.to(".uiux", {opacity: 1, duration: 3})
            tl.current.to(".dev", {opacity: 1, duration: 3, delay: -2.7})
            tl.current.to(".ill", {opacity: 1, duration: 3, delay: -2.7})
            tl.current.to(".ana", {opacity: 1, duration: 3, delay: -2.7})
            tl.current.to(".ani", {opacity: 1, duration: 3, delay: -2.7})
            tl.current.to(".vid", {opacity: 1, duration: 3, delay: -2.7})

        }, serviceRef)
        
    return () => ctx.revert();

    }, []);

    return (
        <Container component="section" ref={serviceRef} sx={{pt:30}} maxWidth={"md"} fixed>
            <Grid container spacing={3} className="serviceGrid">
                <Grid item xs={12}>
                    <Typography variant="h3" component="h4" align="center">Services</Typography>
                </Grid>

                <Grid container direction="row" justifyContent="center" alignItems="center" item xs={12}>
                    <ItemDiv sx={{height:3, width:40, padding:0}} darkhex="#AE7EAA" lighthex="#ff97cc"/>
                </Grid>

                {
                serviceItems.map((items) => {
                    return (
                        <Grid 
                        container 
                        direction="column" 
                        justifyContent="flex-start" 
                        alignItems="center"
                        key={items.id} 
                        item sm={4} 
                        xs={6}
                        sx={{mb:3}}
                        className={items.className}>
                            {items.icon}
                            <Typography variant="h5" gutterBottom>{items.heading}</Typography>
                            <Typography variant="body1" textAlign="center" sx={{px:3}}>{items.body}</Typography>
                        </Grid>
                    )
                })
                }

            </Grid>
        </Container>
    )
}

export default Services